<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import BScroll from 'better-scroll'
// import { callAppRouter, Tool } from '@/js/tool'

export default {
  name: 'home',
  data () {
    return {
      togglebg: false,
      prize_detail: false,
      reward_type: 3, // 1是填写地址 2是领取 3是暂时没有中奖
      home_height: 0,
      click_busy: true,
      toggleBlink: true,
      bink_content: '',
      better_scroll: '',
      detail: 'asd',
      reward_show: false,
      busy: false,
      reward_data: {
        pic: require('../assets/prize_no2.png'),
        desc: '',
        day: 7
      },
      target: 5 // 4号位置停留
    }
  },
  components: {
  },
  methods: {
    toReward () {
      if (!this.busy) {
        this.busy = true
        this.$router.push({ path: '/reward' })
      }
    },
    tgBGDOWN () {
      this.vibration(50) // 振动
      this.togglebg = true
    },
    tgBGUP () {
      this.vibration(50) // 振动
      this.togglebg = false
    },
    bScroll () {
      const wrapper = document.querySelector('#app')
      this.better_scroll = new BScroll(wrapper, {
        click: true,
        bounce: {
          top: false,
          bottom: false,
          left: false,
          right: false
        }
      })
    },
    async clickEvent () {
      // this.$router.push({ path: '/reward' })
      if (this.click_busy) {
        this.click_busy = false
        const z = await new Promise((resolve) => {
          const now = parseInt(document.querySelector('.change-style-item').getAttribute('index'))
          let currentTarget = Math.abs(now - this.target)
          if (now > this.target) {
            currentTarget = 8 - parseInt(now) + this.target
          }
          this.start(1000, resolve, 32 + currentTarget) // 25 + 7  一周期吧
        })
        console.log(z + '完成动画hahahah')
        setTimeout(() => {
          this.reward_type = 2 // 默认为2 嗯我懒
          const type1 = [1, 3, 5, 7]
          const type2 = [2, 6, 8]
          const z = document.querySelector('.item' + this.target + ' img')
          console.log(z)
          this.reward_data.pic = z.getAttribute('src')
          this.reward_data.desc = z.getAttribute('desc')
          if (type1.includes(parseInt(this.target))) {
            this.reward_type = 1
            this.vibration(2000)
          }
          if (parseInt(this.target) === 4) {
            this.reward_type = 3
            this.vibration(100)
          }
          if (type2.includes(parseInt(this.target))) {
            this.reward_type = 2
            this.vibration(1000)
          }
          // const z = document.querySelector('.item' + this.target).innerHTML
          this.reward_data.day = z.getAttribute('day')
          this.reward_show = true
          this.better_scroll.scrollTo(0, 0, 500)
          this.better_scroll.disable()

          setTimeout(() => {
            this.target = this.WJH.randomNum(1, 8) // 仅仅用于测试
            // alert(this.target + '更tag')
            this.$nextTick(() => {
              this.click_busy = true
            })
          }, 200)
        }, 1000)
      }
    },
    start (time, resolve, max) {
      const count = this.Animation(max)
      console.log(count)
      if (count === 'done') {
        resolve('ok')
        return
      }
      if (count <= 4) { // 4
        setTimeout(() => {
          this.start(50, resolve, max) // time - 230
        }, 50)
        return
      }
      if (count >= max - 5) {
        setTimeout(() => {
          this.start(time + 200, resolve, max)
        }, time)
        return
      }
      setTimeout(() => {
        this.start(time, resolve, max)
      }, time)
    },
    Animation: (function Animation () {
      let count = 0
      return function (max) {
        // if (count === 0) return
        count += 1
        console.log('对 这是动画')
        this.addChange(max)
        if (count === max) {
          count = 0 // 不需要null 因为这个东西是可以抽奖多次调用的 无需删除内存
          return 'done'
        }
        return count
      }
    })(),
    addChange: (function Damn () {
      let mark = 1 + 1
      return function (max) {
        for (let i of document.querySelectorAll('.items')) {
          i.classList.remove('change-style-item')
        }
        document.querySelector('.item' + mark).classList.add('change-style-item')
        mark += 1
        if (mark === 9) {
          mark = 1
        }
      }
    })(),
    prizeDetail (e) {
      console.log(document.querySelector('.item' + e + ' img').getAttribute('src'))
      this.detail = document.querySelector('.item' + e).innerHTML
      this.prize_detail = true
      this.better_scroll.disable()
      this.better_scroll.scrollTo(0, 0, 500)
    },
    prizeDetailClose () {
      this.prize_detail = false
      this.reward_show = false
      this.better_scroll.enable()
      console.log('???')
    },
    vibration (time) {
      navigator.vibrate = navigator.vibrate || navigator.webkitVibrate || navigator.mozVibrate || navigator.msVibrate

      if (navigator.vibrate) {
        // 支持
        navigator.vibrate(time)
        console.log('支持设备震动！')
      }
      // navigator.vibrate([500, 300, 400,300])
    }
  },
  mounted () {
    this.busy = false
    this.$nextTick(this.bScroll())
    // this.bScroll()
    this.bink_content = setInterval(() => { // bink 动画
      this.toggleBlink = !this.toggleBlink
    }, 400)
    /* if (Tool.is_cosleep()) {
      callAppRouter('isLogin', {}, (res, ed) => {
        console.log('???gggggg')
        console.log(ed)
      })
    } */
  }
}
</script>

