          left: px2html($right * $rightTime * 3);
        }
        div:nth-of-type(21) {
          bottom: px2html($top - 2px);
          left: px2html($right * $rightTime * 4);
        }
        div:nth-of-type(22) {
          bottom: px2html($top - 2px);
          left: px2html($right * $rightTime * 5);
        }
