          left: px2html($right * $rightTime * 6);
        }
        div:nth-of-type(24) {
          bottom: px2html($top - 2px);
          left: px2html($right * $rightTime * 7);
        }
        div:nth-of-type(25) { // 左下角的点
          bottom: px2html($top + 2px);
          left: px2html(10px);
        }
        // bottom 上面的是
        $top: 10px;
        $right: 2px;
        $rightTime: 3.8;
        div:nth-of-type(26) {
          top: px2html($top * $rightTime);
          left: px2html($right + 2px);
